import React, { Component } from 'react';
import { Row, Col, Form } from 'antd';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import { getReceivable } from 'services/StudentReduction';

import { SelectAjax, InputMoney, InputOnlyNumber } from 'components/common/index';
// import './style.scss';

@Form.create({ name: 'FormConfigValues' })
class FormConfigValues extends Component {
  state = {};

  feeObject = async value => {
    await this.props.form.setFieldsValue({
      receivableId: value,
    });
    await this.props.form.validateFields((err, values) => {
      this.props.dataFormConfigValues(values);
    });
  };

  changeReducePercent = async value => {
    await this.props.form.setFieldsValue({
      reducePercent: value,
    });
    await this.props.form.validateFields((err, values) => {
      this.props.formValue(values);
    });
  };

  changeReduceMoney = async value => {
    await this.props.form.setFieldsValue({
      reduceMoney: value,
    });
    await this.props.form.validateFields((err, values) => {
      this.props.formValue(values);
    });
  };

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const yearEduId = getFieldValue('yearEduId');
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="Năm học">
              {getFieldDecorator('yearEduId', {})(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Học kỳ">
              {getFieldDecorator('semesterId', {})(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSemesters}
                  params={{ yearEduId }}
                  allowGetData={!!yearEduId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Khoản thu">
              {getFieldDecorator('receivableId', {})(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  onChange={this.feeObject}
                  service={getReceivable}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="% giảm">
              {getFieldDecorator('reducePercent', {})(
                <InputOnlyNumber onChange={this.changeReducePercent} persent="true" min={0} />
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Số tiền giảm">
              {getFieldDecorator('reduceMoney', {})(
                <InputMoney onChange={this.changeReduceMoney} min={0} />
              )}
            </Form.Item>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormConfigValues;



